body,
html {
	height: 100%;
}
#mainImg > img {
	/* Full height */
	height: 100vh;
	width: 100vw;
	background-color: transparent;
	background-image: url('/images/png/ninja1.png');
	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

body,
html {
	height: 100%;
}

#brand {
	display: flex;
	text-align: right;
	flex-direction: column;
	padding-top: 40vh;
	padding-bottom: 0px;
	margin-right: 15vw;

	font-family: 'Marcellus SC', serif;
}
.name {
	z-index: 100;
	color: rgb(233, 233, 233);
	font-size: 12rem;
}
#mainImg {
	position: relative;
	width: 100%;
}
#mainImg > div > img {
	position: absolute;
	/* Full height */
	height: 95vh;
	width: 100%;
	background-position: 50% 50%;
	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background: rgb(80, 138, 83);
	-webkit-object-fit: cover;
	-moz-object-fit: cover;
	object-fit: cover;
}

